const map = new Map();

export default {
    // è®¾ç½® Vue å®ä¾‹
    set(id, data) {
        map.set(id, data);
    },

    // è·å– Vue å®ä¾‹
    get(id) {
        return map.get(id);
    },

    // åˆ é™¤ Vue å®ä¾‹
    remove(id) {
        map.delete(id);
    },

    // è°ƒç”¨ Vue å®ä¾‹çš„æ–¹æ³•
    call(id, method, ...args) {
        const inst = map.get(id);
        if (inst?.vm && typeof inst.vm[method] === "function") {
            inst.vm[method](...args);
        }
    },

    // è·å–æ‰€æœ‰ Vue å®ä¾‹
    getAll() {
        return Array.from(map.entries()).map(([id, data]) => ({
            id,
            ...data,
        }));
    },

    // è·å–æ‰€æœ‰ id
    keys() {
        return Array.from(map.keys());
    },

    // æ¸…ç©ºæ‰€æœ‰
    clear() {
        map.clear();
    },
};


