<template>
  <div id="app" class="home">
    <div class="drag-panel" style="position: fixed;z-index: 999999999999;">
      <div v-for="(item,index) in componentsList" :key="index" draggable="true"
           @dragstart="onDragStart($event, JSON.stringify(item))">
        <img :src="item.icon" alt="" draggable="false">
        <p>{{ item.label }}</p>
      </div>
    </div>
    <div id="lf-container" style="width: 100%; height:100vh;overflow: hidden" @drop="onDrop"
         @dragover.prevent="onDragOver"></div>
    <el-button @click="exportButton" style="position: fixed;right: 10px;top: 67px;right: 5px;z-index: 9999999">å¯¼å‡ºæ•°æ®
    </el-button>
    <el-button @click="changeLineColor" style="position: fixed;right: 10px;top: 116px;right: 5px;z-index: 9999999">Edge
      - A
    </el-button>

    <!--    é”šç‚¹èœå•-->
    <div id="anchor-menu">
      <div class="content" v-for="item in anchorMenu" :key="item.id"
           @click="addNodeClick(item.type)">
        <img :src="item.icon" alt="" draggable="false">
        <p>{{ item.name }}</p>
      </div>
    </div>

    <!--    å³é”®èœå•-->
    <div id="custom-menu" v-show="rightMenuShow" :style="{ top: menuPosition.y + 'px', left: menuPosition.x + 'px' }">
      <div class="content" v-for="item in rightMenuShowList" :key="item.id"
           @click="handleMenuClick('delete')">
        <img :src="item.icon" alt="" draggable="false">
        <p>{{ item.name }}</p>
      </div>
    </div>


  </div>
</template>

<script>
import LogicFlow from '@logicflow/core';
import {Control} from "@logicflow/extension";

LogicFlow.use(Control);
import "@logicflow/core/lib/style/index.css";
import {DndPanel} from '@logicflow/extension';
import "@logicflow/extension/lib/style/index.css"

import {defaultEdge, animationEdge, Highlight} from '@/utils/BezierEdge'

// å…¨å±€æ³¨å†Œç®¡ç†å™¨ å‡½æ•°å·¥å‚
import vueInstanceManager from '@/modelVue/js/vueInstanceManager';
import {createVueHtmlNode} from "./js/createVueHtmlNode";

import {componentsList} from "./js/componentsList";
import {nodeData} from "./js/nodeData";
import {anchorMenu} from "./js/anchor-menu";

export default {
  name: 'App',
  data() {
    return {
      lf: '',

      // æ¸²æŸ“æ•°æ®
      nodeData,

      // vueç»„ä»¶ç»„å†Œ
      componentsList,

      // å¯¼å‡ºæ•°æ® è®°å½•vueç»„ä»¶å®Œæˆæƒ…å†µ
      childIds: [],

      // è‡ªå®šä¹‰èœå•
      anchorMenu,

      menuDom: null,
      rightMenuShow: false,

      rightMenuShowList: [
        {
          id: Math.random(),
          name: 'åˆ é™¤',
          icon: require('@/assets/åˆ é™¤.png'),
        },
      ],

      menuPosition: {x: 0, y: 0},

    }
  },
  created() {
  },

  mounted() {
    this.init()
    this.menuDom = document.getElementById('anchor-menu')
    window.addEventListener('click', () => this.menuDom.style.display = 'none')
  },
  methods: {
    init() {
      this.lf = new LogicFlow({
        container: document.querySelector('#lf-container'),
        // edgeType: 'bezier', // line ç›´çº¿ã€polyline æŠ˜çº¿ã€ bezier æ›²çº¿
        plugins: [DndPanel],
        grid: {
          size: 15, // ç‚¹çš„å¯†é›†ç¨‹åº¦
          visible: true,
          type: 'dot', // 'dot' | 'mesh'
          config: {
            color: '#e2e4ed', // ç‚¹çš„é¢œè‰²
            thickness: 1, // ç‚¹çš„å¤§å°
          },

        },
        nodeTextEdit: false,// ç¦æ­¢ä¿®æ”¹nodeå†…å®¹
        edgeTextEdit: false,// ç¦æ­¢ä¿®æ”¹edgeå†…å®¹
        stopScrollGraph: true, // ç¦æ­¢é¼ æ ‡æ»šåŠ¨ç”»å¸ƒ
        stopZoomGraph: true, // ç¦æ­¢ç¼©æ”¾ç”»å¸ƒ
        adjustEdge: false, // ç¦æ­¢ç”¨æˆ·æ‹–åŠ¨ä¸­é—´
        // å–æ¶ˆè¿çº¿è¾¹æ¡†
        edgeSelectedOutline: false,
        hoverOutline: false
      });

      // vue model ç»„ä»¶ç»„å†Œ
      this.componentsList.forEach(item => {
        this.lf.register(createVueHtmlNode({
          type: item.type, // èŠ‚ç‚¹ç±»å‹
          component: item.component,
          properties: item.properties
        }))
      })
      // æ™®é€šmodel æ³¨å†Œ
      this.lf.register(defaultEdge)
      this.lf.register(animationEdge)
      this.lf.register(Highlight)
      // è®¾ç½®å…¨å±€é»˜è®¤degeæ ·å¼
      this.lf.setDefaultEdgeType('EDGE_BEZIER');

      // æ»‘åŠ¨
      this.lf.on('node:mouseenter', ({data}) => {
        const nodeId = data.id;
        const edges = this.lf.graphModel.edges;
        edges.forEach(edge => {
          if ((edge.sourceNodeId === nodeId || edge.targetNodeId === nodeId) && edge.type !== 'EDGE_BEZIER_A') {
            this.lf.changeEdgeType(edge.id, 'Highlight');
          }
        });
      });
      this.lf.on('node:mouseleave', ({data}) => {
        const nodeId = data.id;
        const edges = this.lf.graphModel.edges;
        edges.forEach(edge => {
          if ((edge.sourceNodeId === nodeId || edge.targetNodeId === nodeId) && edge.type !== 'EDGE_BEZIER_A') {
            this.lf.changeEdgeType(edge.id, 'EDGE_BEZIER');
          }
        });
      });

      this.lf.on('node:click', ({data}) => {
        console.log(data, '---------')
        this.rightMenuShow = false
        const nodeId = data.id;
        const vueManager = vueInstanceManager.getAll()
        vueManager.forEach(item => {
          if (item.id === nodeId) {
            item.vm.$el.style.border = '1.5px solid #3f58fd'
          } else {
            item.vm.$el.style.border = 'none'
          }
        })
      });
      this.lf.on('blank:click', () => {
        this.rightMenuShow = false
      })

      this.lf.on('custom:anchorClick', ({node}) => {
        console.log('é”šç‚¹è¢«ç‚¹å‡»/å¼€å§‹è¿çº¿ï¼š', node);
        const menu = document.getElementById('anchor-menu');
        this.anchorMenu = anchorMenu.filter(item => item.type !== 'start-v')
        if (node.type === "start-v") {
          this.anchorMenu = anchorMenu.filter(item => item.type !== 'start-v')
        } else {
          this.anchorMenu = anchorMenu.filter(item => item.type !== 'start-v' && item.type !== node.type)
        }
        menu.style.display = 'block';
      });
      this.lf.on('anchor:click', () => {
        this.rightMenuShow = false
      });

      // ç›‘å¬èŠ‚ç‚¹å³é”®äº‹ä»¶
      this.lf.on('node:contextmenu', ({e, data}) => {
        this.contextmenuFun(e, data, true)
      });

      // ç›‘å¬è¿æ¥çº¿å³é”®äº‹ä»¶
      this.lf.on('edge:contextmenu', ({e, data}) => {
        this.contextmenuFun(e, data, true)
      });

      // ç©ºç™½å¤„å³é”®ä¹Ÿå¯ä»¥ç›‘å¬
      this.lf.on('blank:contextmenu', ({e}) => {
        this.contextmenuFun(e, null, false)
      });

      this.lf.render(this.nodeData);

    },

    onDragStart(event, data) {
      event.dataTransfer.setData("nodeData", data);
    },
    onDrop(event) {
      const nodeData = event.dataTransfer.getData("nodeData");
      let resData = JSON.parse(nodeData);
      if (!resData) return;

      // é’ˆå¯¹ç”»å¸ƒæ”¾å¤§ç¼©å°å¹³ç§»åæ‹–æ‹½ä½ç½®æœ‰åç§»å¤„ç†
      const {SCALE_X, SCALE_Y, TRANSLATE_X, TRANSLATE_Y} = this.lf.getTransform();
      const {offsetX, offsetY} = event;
      this.lf.addNode({
        type: resData.type,
        x: (offsetX - TRANSLATE_X) / SCALE_X,
        y: (offsetY - TRANSLATE_Y) / SCALE_Y,
        properties: resData.properties
      });
    },
    onDragOver(event) {
      event.preventDefault();
      event.dataTransfer.dropEffect = "move"; // è®©é¼ æ ‡ä¸ä¼šå‡ºç°â€œå¤åˆ¶â€åŠ å·
    },

    // å¯¼å‡º
    async exportButton() {
      let graph = this.lf.getGraphData()
      this.childIds = graph.nodes.map(item => item.id);

      const promises = this.childIds.map(
          (id) =>
              new Promise((resolve) => {
                const handler = (childId) => {
                  if (childId === id) {
                    this.bus.$off("childMethodDone", handler);
                    resolve();
                  }
                };
                this.bus.$on("childMethodDone", handler);
              })
      );

      this.bus.$emit("runChildMethod");
      await Promise.all(promises);

      let values = this.lf.getGraphData()// getGraphRawData()è·å–åŸå§‹æ•°æ®ï¼ŒgetGraphData()è·å–åŠ å·¥åçš„æ•°æ®
      console.log(values, '+++++++++++')
    },

    // ä¿®æ”¹é“¾æ¥çº¿é¢œè‰²
    changeLineColor() {
      this.lf.changeEdgeType('5d7482d7-9a0f-438f-ab56-b83062ffc6dc', 'EDGE_BEZIER_A');
    },

    // æ·»åŠ èŠ‚ç‚¹
    addNodeClick(type) {
      const currentNode = window.currentNode;
      if (!currentNode) return;

      const graphModel = currentNode.props.graphModel;
      console.log(currentNode, '-=-=-=-=-=')
      const x = currentNode.props.model.x + 450;
      const y = currentNode.props.model.y;

      const newNode = graphModel.addNode({
        type,
        x,
        y,
        properties: {}
      });

      graphModel.addEdge({
        sourceNodeId: currentNode.id,
        targetNodeId: newNode.id,
      });

      // å…³é—­èœå•
      this.menuDom.style.display = 'none';
    },

    // å³é”®èœå•ç‚¹å‡»
    handleMenuClick() {
      this.lf.deleteNode(this.currentNode.id) || this.lf.deleteEdge(this.currentNode.id);
      this.rightMenuShow = false
    },
    // å³é”®å…¬å…±äº‹ä»¶
    contextmenuFun(e, data, show) {
      e.preventDefault();
      const menu = document.querySelector('#custom-menu')

      const canvasWidth = window.innerWidth;
      const canvasHeight = window.innerHeight;

      const menuWidth = menu.offsetWidth;
      const menuHeight = menu.offsetHeight;

      let x = e.clientX;
      let y = e.clientY;
      // è°ƒæ•´ä½ç½®é˜²æ­¢è¶…å‡ºå³è¾¹ç•Œ
      if (x + menuWidth > canvasWidth) {
        x = canvasWidth - menuWidth - 10;
      }
      // è°ƒæ•´ä½ç½®é˜²æ­¢è¶…å‡ºä¸Šè¾¹ç•Œ
      if (y < 0) {
        y = e.clientY + 10;
      }
      // è°ƒæ•´ä½ç½®é˜²æ­¢è¶…å‡ºä¸‹è¾¹ç•Œ
      if (y + menuHeight > canvasHeight) {
        y = canvasHeight - menuHeight - 10;
      }

      this.currentNode = data;
      this.menuPosition = {x, y};
      this.rightMenuShow = show
    }
  }

};
</script>
<style scoped lang="scss">
@use "@/assets/css/index.scss" as *;
</style>
